import axios from "axios";
// import AuthFirebase from "./auth/authFirebaseDB";

// Creating an instance of axios containing a URL so whenever there is a need to change the url it whould be this place only
export const firebaseAxiosInstance = axios.create({
    baseURL:
        "https://portfolio-2-3c917-default-rtdb.asia-southeast1.firebasedatabase.app/",
});

firebaseAxiosInstance.interceptors.request.use(
    async function (config) {
        // Do something before request is sent
        try {
            //!! NOTE :: Opened read access to firebase's realtime DB so no need of token

            // const accessToken = await AuthFirebase.getAccessToken();
            // if (accessToken === null)
                // throw new Error("Failed to receive access token !");

            // config.headers.Authorization = `Bearer ${accessToken}`;
            return config;
        } catch (error) {
            // reject the api call here itself
            return Promise.reject(error.message);
        }
    },
    function (error) {
        // Do something with request error
        return Promise.reject(error);
    }
);

firebaseAxiosInstance.interceptors.response.use(
    function (response) {
        // Any status code that lie within the range of 2xx cause this function to trigger
        // Do something with response data
        return response;
    },
    function (error) {
        // Any status codes that falls outside the range of 2xx cause this function to trigger
        // Do something with response error
        let msg;
        if (error.toString().indexOf("401") !== -1)
            msg = "At this moment the application is Unauthorized to access the data!";
        else msg = error;
        return Promise.reject(msg);
    }
);

export const mediumAxiosInstance = axios.create({
    baseURL: "https://api.rss2json.com/v1",
});
